.CVForm {
  background-color: white;
  margin-top: 5vh;
  display: flex;
  flex-direction: row;
  width: 70vw;
  min-height: 100vh;
  max-height: 200vh;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  height: fit-content;
}

.Experience {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.Experience__decoration-container {
  width: 100%;
  height: 30%;
  border-bottom: 2px solid black;
  display: flex;
  justify-content: space-between;
}

.Experience__decoration-subcontainer {
  height: 50%;
  width: 70%;
  border-right: 2px solid black;
}

.Experience__decoration-box {
  align-self: center;

  width: 5vw;
  height: 6vh;
  background-color: black;
}
